import React from 'react';
import Game from '../components/Game';
import { Divider, DatePicker } from 'antd';
import moment from 'moment';

class GameList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {games: [], date: moment().format('MMDDYYYY')};
    }

    componentDidMount() {
        this.gamesList();
    }

    componentDidUpdate(prevProps, prevState) {
       if (this.state.date !== prevState.date) {
            this.gamesList();
       }
    }

    onChange(date, dateString) {
        const formattedDate = moment(dateString, "YYYY-MM-DD").format('MMDDYYYY');
        this.setState({date: formattedDate});
    }

    gamesList() {
        fetch('http://127.0.0.1:5000/date/' + this.state.date)
        //fetch('http://127.0.0.1:5000/date/06132019')
            .then(response => response.json())
            .then(data => this.setState({games: data}))
            .catch(function(err) {
                console.log(err);
            });
    }

    render() {
        const games = this.state.games;
        const gamesList = games.map(function(game) {
            return <div key={game.GameID}> <Game {...game} /> <Divider/> </div>;
        });

        if (games.length === 1 && games[0].GameID === 'none') {
            return (
                <div>
                    <DatePicker onChange = {this.onChange.bind(this)} defaultValue={moment()}/>
                    <Divider/>
                    { <h1> No games to display </h1> }
                </div>
            );
        } else {
            return (
                <div>
                    <DatePicker onChange = {this.onChange.bind(this)} defaultValue={moment()}/>
                    <Divider/>
                    { gamesList }
                </div>
            );
        }
    }
}

export default GameList;