import React from 'react';
import { Layout, Menu } from 'antd';
import { NavLink } from 'react-router-dom';

const { Header, Content, Footer } = Layout;

const CustomLayout = (props) => {
    return (
      <Layout className="layout">
        <Header>
          <div className="logo" />
          <Menu
            theme="dark"
            mode="horizontal"
            defaultSelectedKeys={['1']}
            style={{ lineHeight: '64px' }}
          >
            <Menu.Item key="1"><NavLink to='/'>Home</NavLink></Menu.Item>
            <Menu.Item key="2"><NavLink to='/about'>About</NavLink></Menu.Item>
          </Menu>
        </Header>
        <Content style={{ padding: '0 50px' }}>
          <div style={{ background: '#fff', padding: '40px 180px', minHeight: 280 }}>
            {props.children}
          </div>
        </Content>
        <Footer style={{ textAlign: 'center' }}> ©2019 Created by <a href='https://vandyliu.github.io'>Vandy Liu</a></Footer>
      </Layout>
    );
}

export default CustomLayout;
